<template>
  <div>
    <div class="backbutton" @click="goback()">
      <p>Go back</p>
      <p>DHH .COM</p>
    </div>
    <div class="embed-responsive embed-responsive-16by9">
      <iframe
        class="embed-responsive-item"
        :src='"https://www.youtube.com/embed/"+videoid+"?rel=0"'
        allowfullscreen
      ></iframe>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      videoid: ""
    };
  },
  mounted() {
    this.videoid = sessionStorage.getItem("videoid");
    if (!this.videoid) this.$router.push("dashboard");
  },
  methods: {
    goback() {
      this.$router.push("dashboard");
    }
  }
};
</script>
<style scoped>
.backbutton {
  cursor: pointer;
  position: absolute;
  z-index: 999999;
  background: white;
  height: 3rem;
  font-weight: 900;
  width: 7rem;
  top: calc(50vh - 1rem);
  border-radius: 5px;
  padding: 10px;
  -webkit-box-shadow: 0px 0px 81px 0px rgba(250, 250, 250, 0.61);
  -moz-box-shadow: 0px 0px 81px 0px rgba(250, 250, 250, 0.61);
  box-shadow: 0px 0px 81px 0px rgba(250, 250, 250, 0.61);
}
.backbutton:hover {
  background: red;
  transition: all 1s;
  color: white;
  -webkit-box-shadow: 0px 0px 81px 0px rgba(242, 9, 68, 1);
  -moz-box-shadow: 0px 0px 81px 0px rgba(242, 9, 68, 1);
  box-shadow: 0px 0px 81px 0px rgba(242, 9, 68, 1);
}
</style>