import Vue from 'vue'
import Router from 'vue-router'
import fournotfour from '@/components/pages/404'
// import login from '@/components/pages/login'
import dashboard from '@/components/pages/dashboard'
import video from '@/components/pages/videopage'

Vue.use(Router)
export default new Router({
  mode: "history",
  routes: [
    {
      path: '/',
      name: 'home',
      component: dashboard
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: dashboard
    },
    {
        path: '/video',
        name: 'video',
        component: video
      },
    {
      path: '*',
      name: '404',
      component: fournotfour
    }
  ]
})